<template>
  <div class="col-lg-12">
      <div class="col-lg-6 verticalAlign">
          <p>Seleccione un rango de fechas</p>
          <!-- <div class="col-lg-3 verticalAlign">
              <datepicker v-model="fechaInicio" placeholder="Seleccione el Inicio" :minimumView="'month'" :maximumView="'month'"></datepicker>
          </div>
          <br/>
          <br/>
          <div class="col-lg-3 verticalAlign">
              <datepicker v-model="fechaFin" placeholder="Seleccione el Final" :minimumView="'month'" :maximumView="'month'"></datepicker>
          </div> -->
          <b-form-group>
            <template slot="label">
                <b>Seleccione los años:</b><br />
                <b-form-checkbox
                v-model="allSelectedYears"
                :indeterminate="indeterminate"
                aria-describedby="Years"
                aria-controls="years"
                @change="toggleAllYears"
                >
                {{ allSelectedYears ? 'Quitar Todos' : 'Todos' }}
                </b-form-checkbox>
            </template>

            <b-form-checkbox-group
                id="years"
                stacked
                v-model="selectedYears"
                name="flavs"
                :options="years"
                class="ml-4"
                aria-label="Individual years"
            />
            </b-form-group>
      </div>
      <div class="col-lg-6 verticalAlign">
          <b-form-group>
            <template slot="label">
                <b>Seleccione el tipo de evento:</b><br />
                <b-form-checkbox
                v-model="allSelectedEvents"
                :indeterminate="indeterminate"
                aria-describedby="Tipo de Evento"
                aria-controls="events"
                @change="toggleAllEvents"
                >
                {{ allSelectedEvents ? 'Quitar todos' : 'Todos' }}
                </b-form-checkbox>
            </template>

            <b-form-checkbox-group
                id="events"
                stacked
                v-model="selectedEvents"
                name="flavs"
                :options="events"
                class="ml-4"
                aria-label="Individual years"
            />
            </b-form-group>
      </div>
      <div class="col-lg-6 verticalAlign" v-for="(year, index) in yearsS" v-bind:key = "index">
          <!-- <chartGeneral :fechaI ="customFormatter(fechaInicio)" :fechaF="customFormatter(fechaFin)" :event ="eventos"/> -->
         {{ year }} 
          <chartGeneral :year="year" :event ="eventos">
          </chartGeneral>
      </div>
  </div>
</template>

<script>
const axios = require('axios');
var _ = require('lodash');
const moment = require("moment")
import chartGeneral from '../components/charts/chart1'
// import Datepicker from 'vuejs-datepicker';

  export default {
    components: {
      chartGeneral //, Datepicker
    },
    data() {
      return {
        fechaInicio: '',
        fechaFin: '',
        years: [],
        events: [],
        selectedYears: [],
        selectedEvents: [],
        allSelectedYears: false,
        allSelectedEvents: false,
        indeterminate: false,
        eventos: [],
        yearsS: []
      }
    },
    mounted () {
        let personForm = this.toFormData({opcion: 'yearSistem'});
        axios
        .post('./mysqli.php', personForm)
        .then(response => {
            _.forEach(response.data, (elementc) => {
                 this.years.push(elementc)
            })
        })
        .catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
            this.errored = true
        })
        .finally(() => this.loading = false)
        let personForm2 = this.toFormData({opcion: 'typeEvents'});
        axios
        .post('./mysqli.php', personForm2)
        .then(response => {
            _.forEach(response.data, (elementc) => {
                 this.events.push(elementc)
            })
        })
        .catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
            this.errored = true
        })
        .finally(() => this.loading = false)
    },
    methods: {
    customFormatter: function(date) {
        return moment(date).format('YYYY-MM-DD');
    },
    toFormData: function(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
        }
        return formData;
      },
      toggleAllYears(checked) {
        this.selectedYears = checked ? this.years.slice() : []
      },
      toggleAllEvents(checked) {
        this.selectedEvents = checked ? this.events.slice() : []
      }
    },
    watch: {
      selectedEvents(val) {
        this.eventos = []
        this.eventos = val
  
      },
      selectedYears (val) {
        this.yearsS = val
      }
    }
  }
</script>
<style>
.verticalAlign {
    vertical-align: top;
    display: inline-block;
}
</style>
